<template>
  <view v-if="visible" class="login-mask">
    <view class="login-box">
      <text class="title">欢迎回来</text>
      
      <view class="input-group">
        <button class="wechat-btn" @click="handleWechatLogin">
          <text class="icon">W</text> 微信一键登录
        </button>
      </view>

      <view class="divider">
        <view class="line"></view>
        <text class="text">或账号登录</text>
        <view class="line"></view>
      </view>

      <view class="form">
        <input class="input" v-model="form.username" placeholder="用户名/手机号" />
        <input class="input" v-model="form.password" type="password" placeholder="密码" />
        <button class="login-btn" @click="handlePwdLogin">登录</button>
        <text class="skip-btn" @click="close">暂不登录</text>
      </view>
    </view>
  </view>
</template>

<script setup>
import { reactive } from 'vue';
import { useUserStore } from '@/store/user';

const props = defineProps({ visible: Boolean });
const emit = defineEmits(['update:visible', 'success']);
const userStore = useUserStore();

const form = reactive({ username: '', password: '' });

const close = () => {
  emit('update:visible', false);
};

const handleWechatLogin = async () => {
  // Mock login
  await userStore.login('wechat_user', '123456');
  emit('success');
  close();
};

const handlePwdLogin = async () => {
  if (!form.username) return;
  await userStore.login(form.username, form.password);
  emit('success');
  close();
};
</script>

